:48px;margin-bottom:-20px;font-weight:700}#Main .brandValue .circle.rounded{width:320px;border-radius:50%;background-position-x:585px;color:#1e3926;box-shadow:rgba(0,0,0,.2) 10px 15px 15px}#Main .valueDetail{display:none;opacity:0;transition:opacity 1s}#Main .valueDetail.visible{display:block;opacity:1}